@charset "UTF-8";
#About .title-panel .title-text {
  line-height: 15.1px !important;
}
#About .inner {
  display: flex;
}
#About .inner .summary-panel {
  width: 520px;
}
#About .inner .summary-panel .paragraph-list .bold-text {
  color: #64FFDA;
  font-weight: 400;
}
#About .inner .summary-panel .skills-list {
  display: grid;
  grid-template-columns: repeat(3, minmax(140px, 200px));
  margin: 20px 0px 0px;
  overflow: hidden;
  list-style: none;
}
#About .inner .summary-panel .skills-list .skill-item {
  letter-spacing: 0.75px;
  margin-bottom: 10px;
}
#About .inner .summary-panel .skills-list li::before {
  content: "â€¢";
  color: #64FFDA;
}
#About .inner .profile-panel {
  width: 300px;
  position: relative;
  overflow: hidden;
}
#About .inner .profile-panel img {
  position: absolute;
  right: 0;
  width: 250px;
  height: 350px;
}